@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  margin: 0;
  padding: 0;
}
html {
  font-size: 20px;
}
body {
  height: 100vh;
  color: white;
  font-family: Roboto;
  background-color: white;
  // background-size: cover;
  &.notScrollable {
    overflow: hidden;
  }
}
.hidden {
  display: none;
}
.cover {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.5);
  z-index: 10;
}
