.main {
  width: 100vw;
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  // margin-top: 3%;
  // margin-left: 4%;
}
.button {
  box-sizing: border-box;
  box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  border: 2px solid lightsteelblue;

  color: white;
  background: lightsteelblue;
  text-align: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  transition: 0.5s;
  &:hover {
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    background: linear-gradient(lightsteelblue, lemonchiffon);
    border: 2px solid lemonchiffon;
  }
  &:active {
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    background: lightsteelblue;
    border: 2px solid lightsteelblue;
  }
}
#player-one-btn {
  width: 50%;
  height: 20%;
}
#player-two-btn {
  width: 50%;
  height: 20%;
}
#start-btn {
  width: 10%;
  height: 20%;
  border-radius: 15%;
}
.player-one {
  width: 20%;
  height: 40%;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
