@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}html{font-size:20px}body{height:100vh;color:white;font-family:Roboto;background-color:white}body.notScrollable{overflow:hidden}.hidden{display:none}.cover{position:absolute;bottom:0px;left:0px;width:100vw;height:100vh;background-color:rgba(255,255,255,0.5);z-index:10}

.footer{width:100vw;height:10vh;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;font-size:20px;background-color:lemonchiffon;color:lightsteelblue}#rs-school{margin-right:3%}

.header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:100vw;height:10vh;background-color:lightsteelblue;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}.img{margin-left:3%}

.main{width:100vw;height:80vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.button{box-sizing:border-box;box-shadow:0px 2px 7px rgba(0,0,0,0.2);border-radius:3px;border:2px solid lightsteelblue;color:white;background:lightsteelblue;text-align:center;font-family:Roboto;font-style:normal;font-weight:500;font-size:14px;line-height:16px;letter-spacing:1.25px;text-transform:uppercase;transition:0.5s}.button:hover{text-shadow:0px 4px 4px rgba(0,0,0,0.15);background:linear-gradient(lightsteelblue, lemonchiffon);border:2px solid lemonchiffon}.button:active{text-shadow:0px 4px 4px rgba(0,0,0,0.15);background:lightsteelblue;border:2px solid lightsteelblue}#player-one-btn{width:50%;height:20%}#player-two-btn{width:50%;height:20%}#start-btn{width:10%;height:20%;border-radius:15%}.player-one{width:20%;height:40%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;grid-gap:20px;gap:20px}


